.tab {
    position: relative;
}

.tab:before {
    content: "";
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgb(199, 221, 231);
    opacity: 0.85;
}

/*.form-horizontal*/
.form-control {
    /* инпуты */
    height: 40px;
    background-color: #6490B1;
    border: none;
    border-radius: 20px;
    box-shadow: none;
    padding: 0 20px;
    font-size: 14px;
    font-weight: bold;
    color: #000;
    transition: all 0.3s ease 0s;
}

/*.form-horizontal*/
.btn {
    width: 100%;
    background: #14489E;
    padding: 10px 20px;
    border: none;
    font-weight: bold;
    color: #fff;
    border-radius: 20px;
    text-transform: uppercase;
    margin: 20px 0 30px 0;
}

/*.form-horizontal*/
.btn:focus {
    outline: none;
    box-shadow: inset 2px 2px 2px #000;
}

.tableEmitator {
    display: table;
    width: 100%;
    height: 600px;
}

.cellEmitator {
    display: table-cell;
    vertical-align: middle;
}

.centeredBlock {
    min-width: 200px;
    max-width: 2000px;
    margin: 0 auto;
    vertical-align: middle;
}

h1 {
    text-align: center;
    color: white;
}

label {
    color: #000;
}

.tab {
    padding: 30px 50px;
}

.tab:before {
    border-radius: 20px;
}

.form-group {
    position: relative;
}

.forgot {
    color: currentColor;
}

.form-horizontal .btn {
    min-width: 50px;
    width: auto;
    margin: 0;
}

button {
    min-width: 50px;
}

@media (min-width: 768px) {
    h1 {
        margin-top: 60%;
    }
}
